<template>
	<view class="wrapper padding">
		<view class="list"  v-if="!empty">
			<view class="item" v-for="(item,index) in listData" :key="index">
				<view class="info">
					<!-- <image src="../../static/award.png" class="avatar"></image> -->
					<view class='other'>
						<view class='name_w'>
							<text class="name">{{item.memberName}}</text>
						</view>
						<view class='tel' @click="tel(item.memberMobile)">{{item.memberMobile}}</view>
					</view>
				</view>
				<view class="orderList">
					<view 
						v-for="(litt,idx) in item.orderList" 
						:key="idx" 
						class='order-wrapper'
				  >	
						<view class="order_item rowo">
							<view class='txt desc'>意向车型</view>
							<view class='txt want'>{{litt.carTypeName}}</view>
							<view class='status'>
								<text class='shape'></text>
								<text class='txt'>{{litt.nodeStepName}}</text>
								<text class='cancel-order flex-row-center' v-if="litt.orderState==-1">订单已取消</text>
							</view>
						</view>
						<view class="order_item">
							<view class='txt desc t'>创建时间:</view>
							<view class='txt  t'>{{litt.createTimeStamp?litt.createTimeStamp:''}}</view>
						</view>
						<view class="order_item">
							<view class='txt desc t'>最近修改时间:</view>
							<view class='txt  t'>{{litt.lastModifyTimeStamp?litt.lastModifyTimeStamp:''}}</view>
						</view>
						<view class="order_item flex-row-end">
							<button class='cu-btn control_btn bg-white' @click="goEdit(litt,item)"
								v-if="litt.orderState!=-1 && litt.orderState<5"
							>编辑订单</button>
							<button class='cu-btn control_btn bg-white' @click="goDetail(litt,item)">购车进度查看</button>
						</view>
					</view>
				</view>
			</view>
			<loadMore :status="status" :content-text="textDesc"></loadMore>
		</view>
		<view class="no_data" v-else>
			暂无数据
		</view>
	</view>
</template>
<script>
	import loadMore from "../../components/loadMore/loadMore.vue"
	import {formatTime} from "../../common/tools.js"
	export default {
		data() {
			return {
				textDesc:{contentdown: "加载更多",contentrefresh: "正在加载...",contentnomore: "没有更多数据了"},
				status:"noMore", //可选值：more（loading前）、loading（loading中）、noMore（没有更多了
				empty:false,
				listData:[],  //分页
			};
		},
		methods:{
			goDetail(currentOrder,item){
				this.perfectNavigateTo({
					url:`/pages/staff/staffLookProgress?currentOrderId=${currentOrder.orderId}&clientOrder=${JSON.stringify(item)}`
				})
			},
			
			goEdit(item){
				this.perfectNavigateTo({
					url:`/pages/staff/editOrder?orderInfo=${JSON.stringify(item)}&fromType=2`
				})
			},
			getList(){
				this.request("/order/staff/members/orders").then((res)=>{
					if(res.resultCode==0){
						let _data=res.data
						if(_data.length>0){ //如果有数据
							this.empty=false
							_data.forEach((item,index)=>{
								item.orderList.forEach((lett)=>{
									lett.createTimeStamp=formatTime(lett.createTimeStamp)
									lett.lastModifyTimeStamp=formatTime(lett.lastModifyTimeStamp)
								})
							})
							this.listData=_data
						}else{ //如果这次没数据
							this.empty=true
						}
						this.status="noMore"
					}else{
						uni.showToast({title:res.resultMsg?res.resultMsg:'网络异常,请稍后重试',icon:'none'})
					}
					uni.stopPullDownRefresh()
				})
			},
			tel(tel){
				uni.showModal({
					title:"提示",
					content:"是否确认拨打电话",
					success:(res)=>{
						if(res.confirm){
							uni.makePhoneCall({
							  phoneNumber:tel //仅为示例，并非真实的电话号码
							})
						}
					}
				})
			},
		},
		components:{
			loadMore
		},
		onShareAppMessage(){
			return {
				title:'卫蓝购',//String	是	分享标题	
				path:'/pages/infomation/index',//String	是	页面 path ，必须是以 / 开头的完整路径。	QQ小程序不支持
				imageUrl:"/static/share.png"
			}
		},
		onPullDownRefresh(){ //下拉刷新  无分页
			this.listData=[]
			this.getList()
		},
		onReachBottom(){ //加载更多
			if(this.status!="noMore"){
				this.getList()
			}
		},
		onShow(){
			uni.startPullDownRefresh();
		},
	}
</script>
<style lang="stylus" scoped>
	.wrapper
		.list
			.item
				margin-top:80upx
				&:first-child
					margin-top:20upx
				.info
					display:flex
					flex-direction row
					align-items center
					.avatar
						width:108upx
						height:108upx
						border-radius 50%
						overflow hidden
						margin-right:40upx
					.other
						.name_w
							display:flex
							flex-direction row
							align-items center
							margin-bottom:10upx
							.name
								font-size:32upx;
								font-family:PingFangSC-Semibold,PingFang SC;
								font-weight:600;
								color:rgba(30,30,30,1);
								margin-right:20upx
							.status
								height:30upx;
								line-height 30upx
								text-align center
								border-radius:4upx;
								padding:5upx 10upx
								font-size:24upx
								color:#fff
								&.unpass
									background:rgba(112,208,151,1);
								&.pass
									background:#B2B6C0
				.orderList
					// margin-left:143upx
					margin-top:30upx
					.order-wrapper
						background:rgba(248,249,252,1);
						border-radius:8upx
						padding:0upx 20upx 20upx 20upx
						&:not(:first-child)
							margin-top:20upx
						.order_item
							display:flex
							margin-top:16upx
							flex-direction row
							align-items center
							&.rowo
								height:82upx;
								border-bottom:2upx solid #E3E9F0
								margin-top:0upx
							.txt
								color:#000000
								font-size:24upx
								&.t
									font-size:24upx;
									font-family:PingFangSC-Regular,PingFang SC;
									font-weight:400;
									color:rgba(100,100,100,1);
								&.desc
									margin-right:20upx
								&.want
									flex-basis:200upx
									text-overflow ellipsis;
									overflow hidden
									white-space:nowrap
							image
								width:26upx
								height:26upx
							.status
								display:flex
								flex-direction row
								align-items center
								flex:1
								justify-content flex-end
								.shape
									width:8upx;
									height:8upx;
									transform rotate(45deg)
									background:linear-gradient(135deg,rgba(44,143,255,1) 0%,rgba(23,108,221,1) 100%);
									border-radius:2upx;
								.txt
									color:#0075D6
									font-size:24upx
									margin:0upx 10upx
								.cancel-order
									background:rgba(225,121,105,1);
									border-radius:4upx;
									padding:0upx 8upx
									height:34upx
									color:#fff
									font-size:24upx
						.control_btn
							margin-left:20upx
							color:#646464
							border:1upx solid rgba(100,100,100,1);
</style>			
	
